.App{
  position:absolute;
  width:100vw;
  height:100vh;
  background-color:lightcyan;
  display:grid;
  align-items:center;
  justify-content: center;
}

h1{
  font-family: serif;
  color: transparent;
  text-align: center;
  animation: effect 2s linear infinite;
}

@keyframes effect {
  0% {
      background: linear-gradient(
          #008000, #00FF00);
      -webkit-background-clip: text;
  }

  100% {
      background: linear-gradient(
          #3CE7D7, #000FFF);
      -webkit-background-clip: text;
  }
}